<template>
  <div>
    <top-bar :firstPage="topBar"></top-bar>
    <form action="/阿达">
      <van-search
        placeholder="请输入搜索寝室号关键词"
        show-action
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell v-for="item in list" :key="item.id">
        <template slot="title">
          <span class="custom-text">
            寝室楼 {{ item.stuDormitory }} 楼--- {{ item.stuBedroom }} 寝室---
            {{ item.stuName }}</span
          >
          <van-button
            style="float: right"
            type="default"
            size="mini"
            @click="showdelNotice(item.id)"
            ><van-icon name="delete" />
          </van-button>
          <van-tag type="danger" style="float: right">未归寝</van-tag>
        </template>
      </van-cell>
    </van-list>
    <van-dialog
      v-model="showDel"
      title="删除"
      message="是否确认删除？确认删除学生将从未归寝状态变为归寝！！！"
      show-cancel-button
      @confirm="confirmReturn"
    >
    </van-dialog>
  </div>
</template>

<script>
import TopBar from '@/components/TopBar.vue'
import { isReturnAPI, returnAPI } from '@/api/api'
export default {
  name: 'clockIndex',
  data () {
    return {
      topBar: '归寝情况管理页面',
      loading: false,
      finished: false,
      showDel: false,
      delReturnId: '',
      list: [],
      show: false
    }
  },
  components: {
    TopBar
  },
  methods: {
    onSearch () {},
    onCancel () {
      location.reload()
    },
    async onLoad () {
      // 异步更新数据
      setTimeout(async () => {
        const res = await isReturnAPI()
        this.list = res.data
        if (this.list.length >= res.data.length) {
          this.finished = true
        }
      }, 0)
    },
    async confirmReturn () {
      this.show = true
      const res = await returnAPI(this.delReturnId)
      console.log(res)
      this.$router.go(0)
    },
    showdelNotice (id) {
      this.dialogTitle = '确定删除当前公告吗?'
      this.delReturnId = id
      console.log(this.delReturnId)
      this.showDel = true
    }
  }
}
</script>

